<script>
import { useFileStore, useGlobalStore, useTemplateStore } from '@/store'
import { mapActions, mapWritableState } from 'pinia'

import debug from 'debug'

import {
  BadgeDesign,
  CardDesign,
  CertificateDesign,
} from '@/constants/designPresets'
import SuccessModal from '@/modals/SuccessModal.vue'
import { TEMPLATE_EDITOR_CREATE, UPLOAD_FILE } from '@/store/actions'

const logDebug = debug('ChoosePresetTemplate')

const API_URL_ASSETS = import.meta.env.VITE_APP_API_URL_ASSETS

export default {
  components: {
    SuccessModal,
  },
  data() {
    return {
      designType: 'Badge',
      designTypeError: false,
      templateName: null,
      templateNameError: false,
      photoFromFile: null,
      photoFile: null,
      photoFileError: false,
      hasErrors: false,
      errorMsg: null,
      loading: false,
      message: 'design',
      showConfirmCreateModal: false,
      tid: null,
      timeout: null,
      API_URL_ASSETS,
    }
  },
  computed: {
    isDisabled() {
      return (
        this.designTypeError || this.templateNameError || this.photoFileError
      )
    },
    ...mapWritableState(useTemplateStore, ['layoutBackgroundColor']),
    ...mapActions(useTemplateStore, ['setTemplate']),
  },
  mounted() {
    const el = document.getElementById('fileUpload')

    if (!el) {
      return
    }

    el.addEventListener('change', (event) => {
      if (event.target.files.length !== 0) {
        this.photoFile = event.target.files[0]
        this.photoFromFile = URL.createObjectURL(this.photoFile)
        this.photoFileError = false
      }
    })
  },
  methods: {
    handleClose() {
      logDebug('handleClose', this.tid)
      clearTimeout(this.timeout)
      if (!this.tid) {
        return
      }

      this.$router.push({
        name: `ViewTemplate`,
        params: { tid: this.tid },
      })
    },
    validateDesign() {
      this.hasErrors = false

      if (!this.designType) {
        this.designTypeError = true
        this.hasErrors = true
      }

      if (!this.templateName) {
        this.templateNameError = true
        this.hasErrors = true
      }

      if (!this.photoFile) {
        this.photoFileError = true
        this.hasErrors = true
      }

      return !this.hasErrors
    },
    async addDesign() {
      this.loading = true

      if (!this.validateDesign()) {
        return
      }
      try {
        this.$debug('addDesign', this.designType)
        let preview
        switch (this.designType) {
          case 'Badge':
            preview = `${API_URL_ASSETS}/backgrounds/badge-design.png`

            BadgeDesign.components[1].src = await useFileStore()[UPLOAD_FILE]({
              file: this.photoFile,
              folder: 'logos',
            })
              .catch((err) => {
                console.error(err)
              })
            this.setTemplate(BadgeDesign)
            break
          case 'Certificate':
            preview = `${API_URL_ASSETS}/backgrounds/certificate-design.png`

            CertificateDesign.components[1].src = await useFileStore()[UPLOAD_FILE]({
              file: this.photoFile,
              folder: 'logos',
            })
              .catch((err) => {
                console.error(err)
              })
            this.setTemplate(BadgeDesign)
            break
          case 'Card':
            preview = `${API_URL_ASSETS}/backgrounds/card-design.png`

            CardDesign.components[7].src = await useFileStore()[UPLOAD_FILE]({
              file: this.photoFile,
              folder: 'logos',
            })
              .catch((err) => {
                console.error(err)
              })

            this.setTemplate(BadgeDesign)
            this.layoutBackgroundColor = 'transparent'

            break
          default:
            break
        }

        useTemplateStore().setTemplateName(this.templateName)
        const { tid } = await useTemplateStore()[TEMPLATE_EDITOR_CREATE]({ preview })

        logDebug(tid)
        this.tid = tid
        this.showConfirmCreateModal = true

        this.timeout = setTimeout(() => {
          this.handleClose()
        }, 1000 * 3)
      } catch (error) {
        console.error(error)
      } finally {
        this.loading = false
      }
    },
    chooseFiles() {
      document.getElementById('fileUpload').click()
    },
  },
}
</script>

<template>
  <v-container class="preset px-0">
    <v-row>
      <v-col cols="6" class="">
        <h2 class="title_header">
          {{ $t('credentialDesign.preset.ChoosePresetTemplate') }}
        </h2>
      </v-col>
      <v-col cols="6" class="text-right">
        <v-btn
          :disabled="isDisabled"
          :loading="loading"
          class="next"
          @click="addDesign"
        >
          {{ $t('credentialDesign.addDesignButton') }}
        </v-btn>
      </v-col>
      <v-col cols="12" class="">
        <v-card elevation="0">
          <v-container class="text-left">
            <v-row>
              <v-col cols="12" class="">
                <h3 class="subtitle_text_strong">
                  {{ $t('credentialDesign.preset.subtitle') }}
                </h3>
              </v-col>
              <v-col cols="12" md="10" class="input-field pr-md-10">
                <v-radio-group
                  v-model="designType"
                  row
                  hide-details
                  :class="{ 'error-radio': designTypeError }"
                  class="mt-0"
                  @change="designTypeError = false"
                >
                  <v-radio
                    color="#009fb1"
                    :label="$t('credentialDesign.preset.types[0]')"
                    :value="$t('credentialDesign.preset.types[0]')"
                  />
                  <v-radio
                    color="#009fb1"
                    :label="$t('credentialDesign.preset.types[1]')"
                    :value="$t('credentialDesign.preset.types[1]')"
                  />
                  <v-radio
                    color="#009fb1"
                    :label="$t('credentialDesign.preset.types[2]')"
                    :value="$t('credentialDesign.preset.types[2]')"
                  />
                </v-radio-group>
              </v-col>
              <v-col
                cols="12"
                md="10"
                class="d-flex justify-space-between align-start flex-wrap pr-md-10"
              >
                <v-img
                  max-width="186px"
                  contain
                  :src="`${API_URL_ASSETS}/backgrounds/badge-design.png`"
                  @click="designType = $t('credentialDesign.preset.types[0]')"
                />
                <v-img
                  max-width="270px"
                  contain
                  :src="`${API_URL_ASSETS}/backgrounds/certificate-design.png`"
                  @click="designType = $t('credentialDesign.preset.types[1]')"
                />
                <v-img
                  max-width="270px"
                  contain
                  :src="`${API_URL_ASSETS}/backgrounds/card-design.png`"
                  @click="designType = $t('credentialDesign.preset.types[2]')"
                />
              </v-col>
            </v-row>
            <v-row class="mt-10">
              <v-col cols="12" class="input-field">
                <label class="">
                  {{ $t(`credentialDesign.preset.label.${designType}`)
                  }}<span style="color: #e95e5e"> * </span></label>
                <v-text-field
                  v-model="templateName"
                  class="mt-2"
                  flat
                  solo
                  :error="templateNameError"
                  @input="templateNameError = false"
                />
              </v-col>
              <v-col cols="12" class="input-field">
                <div class="wrapper">
                  <div
                    v-if="photoFromFile"
                    class="uploaded-image"
                    :style="`background-image: url('${photoFromFile}')`"
                  />
                  <p class="small_text">
                    {{ $t(`credentialDesign.preset.photoLabel.${designType}`) }}
                  </p>
                  <p class="mb-0 small_text">
                    {{ $t('createCertModal.upload[1]') }}
                  </p>
                  <p class="small_text">
                    {{ $t('createCertModal.upload[2]') }}
                  </p>
                  <input
                    id="fileUpload"
                    type="file"
                    hidden
                    accept="image/x-png,image/gif,image/jpeg"
                  />
                  <v-btn
                    class="back"
                    :class="{ 'error-btn': photoFileError }"
                    @click="chooseFiles()"
                  >
                    {{ $t('credentialDesign.uploadLogo') }}
                  </v-btn>
                </div>
              </v-col>
            </v-row>
          </v-container>
        </v-card>
      </v-col>
    </v-row>
    <SuccessModal
      v-if="showConfirmCreateModal"
      :message="message"
      :design-type="designType.toLowerCase()"
      @close="handleClose()"
    />
  </v-container>
</template>

